import profileimg from "../images/profile.png"
import studyimg from "../images/study2.png"
import pdfimg from "../images/Pdf.png"
import githubimg from "../images/Github.png"
import { useEffect, useState,useRef } from "react"
import { motion,useScroll } from "framer-motion"


const Intro = () => {
    const [isLoading,setIsLoading] = useState(true);
    
    
    

    useEffect(()=>{
        setTimeout(()=>{setIsLoading(false)},1500)
    },[])

    const handleOpenGithub = () => {
        window.open("https://github.com/RunnerJinHyeon")
    }

    const handleOpenBlog = () => {
        window.open("https://jinee0903.tistory.com/")
    }

    
    
    
    return (
        <>
            <div className="w-full h-screen  flex flex-col justify-center items-center bg-[#F2F2F2]">
                
                {isLoading ? (
                    <motion.div className="box"
                    initial={{ opacity: 0 }}
                    animate={{ opacity: 1 }}
                    transition={{duration: 1}}>
                    <div className="flex items-center justify-center  w-[50vh] h-[30vh] ">
                    <div className=" text-center text-4xl font-bold"></div>
                    </div>
                    </motion.div>
                
                    ) 
                : (
                    <motion.div 
                    initial={{ opacity: 0, y: -98 }}
                    animate={{ opacity: 1, y: 0 }}
                    transition={{duration: 2, delay: 2}}>
                        <div className=" w-[75vh] h-[45vh] bg-opacity-10  flex flex-col justify-center items-center">
                            <div className="flex flex-row w-[75vh] h-[30vh] justify-center">
                                
                                <div className="w-[50vh]" >
                                    <h1 className="text-center w-[50vh] text-3xl my-4">React </h1>
                                    <h1 className="text-center w-[50vh] text-3xl my-4"> </h1>
                                    <h1 className="text-4xl mb-2 w-[50vh] h-[100px] text-center  font-bold text-gray-700"></h1>
                                </div>
                            </div>
                            
                            <div>
                                <div className="flex flex-row w-[350px] h-[100px] justify-between items-center font-bold text-gray-700">
                                    <a href='/assets/portfolio.pdf' target="_blank" className="w-[100px] h-[50px] inline-flex flex-row justify-center items-center rounded-md shadow-xl bg-[#ffffff]">
                                        {/* <img className="w-[18px] h-[18px] mr-1" src={pdfimg} alt="pdf"></img> */}
                                        Resume
                                    </a>
                                    <button className="w-[100px] h-[50px] inline-flex flex-row justify-center items-center rounded-md shadow-xl bg-[#ffffff]" onClick={handleOpenGithub}>
                                        {/* <img className="w-[18px] h-[23px] mr-1" src={githubimg} alt=""/> */}
                                        Github
                                    </button>
                                    <button className="w-[100px] h-[50px] inline-flex flex-row justify-center items-center rounded-md shadow-xl bg-[#ffffff]" onClick={handleOpenBlog}>Blog</button>
                                </div>
                            </div>
                        
                        </div>
                    </motion.div>)}
            </div>
        </>
    )
}

export default Intro